<template>
    <div class="section">
        <ul class="collection">
            <li class="collection-item" v-for="exp in expenses">
                {{ exp.id }} - {{ exp.thing }} - {{ exp.cost }}
            </li>
        </ul>
    </div>
</template>

<script>
import Dexie from 'dexie'

export default {
  name: 'reports',
  data () {
    return {
      expenses: [],
      report: 0,
      num: 0,
      db: null
    }
  },
  mounted () {
    this.db = new Dexie('expensesDb')
    this.db.version(1).stores({
      expenses: `++id, thing, cost, date`
    })
    this.db.open().catch(function (e) { alert('Open failed: ' + e) })

    console.log(this.db.expenses.toArray())

    this.db.expenses.each(function (exp) {
      console.log(exp.id)
    }).catch(function (error) {
        console.error(error)
    })
    
    this.report = this.expenses.reduce((prev, cur) => prev + 10, 0)

    console.log(this.report)

    var accounts = [
      { name: 'James Brown', msgCount: 123 },
      { name: 'Stevie Wonder', msgCount: 22 },
      { name: 'Sly Stone', msgCount: 16 },
      { name: 'Otis Redding', msgCount: 300 }
    ]

    var msgTotal = accounts.reduce(function (prev, cur) {
      return prev + cur.msgCount
    }, 0)

    console.log(msgTotal)
  }
}
</script>

<style>

</style>


